import React, { Component } from "react";
import { Row, Col, Button } from "reactstrap";
import {} from "reactstrap";
import ModuleViewHome from "./ModuleViewHome";
import ModuleViewPendientes from "./ModuleViewPendientes";
import ModulViewProcess from "./ModuleViewProcess";
import ModuleViewProveedores from "./ModuleViewProveedores";
import NavBar from "./navbar";
import "./infome.css";
import ModuleViewDetails from "./ModuleViewDetails";
window.noempl = 37604;
class Main extends Component {
  usuario_con = (numero) => {
    let data = {
      sesionid: numero,
    };
    const requestInfo = {
      method: "POST",
      body: JSON.stringify(data),
      header: new Headers({
        "Content-Type": "application/json",
        Accept: "application/json",
      }),
    };
    //console.log(data);
    fetch(
      "https://Diniz.com.mx/diniz/servicios/services/pn_sesion_con2.php",
      requestInfo
    )
      .then((response) => response.json())
      .then((DatosUsuario) => {
        //console.log(DatosUsuario);
        if (DatosUsuario[0].usuarios.noempl !== "x") {
          this.setState({ nombre: DatosUsuario[0].usuarios.nombre });
          this.setState({ noempl: DatosUsuario[0].usuarios.noempl });
          this.setState({ curnoempl: DatosUsuario[0].usuarios.noempl });
          this.setState({ puesto: DatosUsuario[0].usuarios.puesto });
          this.setState({ DatosUsuario: DatosUsuario[0].usuarios });
          this.setState({ sesionid: DatosUsuario[0].usuarios.uniqueid });
          window.empl = DatosUsuario[0].usuarios.noempl;
        } else {
          this.setState({ DatosUsuario: [] });
          window.location.replace("https://Diniz.com.mx/index.html");
        }
        //console.log(DatosUsuario);
      })
      .catch((e) => console.log(e));
  };
  getPerfil = (noempl) => {
    let data = {
      noempl: noempl,
    };
    const requestInfo = {
      method: "POST",
      body: JSON.stringify(data),
      header: new Headers({
        "Content-Type": "application/json",
        Accept: "application/json",
      }),
    };
    fetch(
      "https://Diniz.com.mx/diniz/servicios/services/ti_getPerfil.php",
      requestInfo
    )
      .then((response) => response.json())
      .then((DatosPerfil) => {
        if (DatosPerfil[0].perfilid !== "x") {
          //console.log(DatosPerfil);
          this.setState({ perfilid: DatosPerfil[0].perfilid });
        } else {
          this.setState({ DatosPerfil: [] });
          window.location.replace("https://Diniz.com.mx/gd/index.html");
        }
      })
      .catch((e) => console.log(e));
  };
  getQueryVariable = (variable) => {
    var query = window.location.search.substring(1);
    var vars = query.split("&");
    for (var i = 0; i < vars.length; i++) {
      var pair = vars[i].split("=");
      if (pair[0] === variable) {
        return pair[1];
      }
    }
    return false;
  };
  componentDidMount() {
    if (this.getQueryVariable("id") !== false) {
      let quer = this.getQueryVariable("id");
      this.usuario_con(quer);
    } else {
      window.location.replace("https://diniz.com.mx/");
    }
  }
  goSalir = () => {
    let myurl = "https://diniz.com.mx/index.html?id=" + this.state.sesionid;
    window.location.replace(myurl);
  };

  constructor() {
    super();
    this.state = {
      modulo: 0,
    };
  }

  selectModule = (modul) => {
    this.setState({ modulo: modul });
  };
  // ModuleViewProveedore
  render() {
    return (
      <div>
        <div style={{ background: "#AED6F1" }}>
          <Row style={{ paddingTop: 10 }}>
            <Col sm={2}></Col>
            <Col sm={8} style={{ textAlign: "center", color: "white" }}>
              <h2>Control de pagos</h2>
            </Col>
            <Col sm={2} style={{ textAlign: "left" }}>
              <Button
                outline
                color="danger"
                size="sm"
                id="toggler4"
                onClick={this.goSalir.bind()}
                style={{ marginBottom: "1rem" }}
              >
                Salir
              </Button>
            </Col>
          </Row>
        </div>
        <NavBar selectModule={this.selectModule}></NavBar>
        {this.state.modulo === "viewProcess" ? (
          <ModulViewProcess />
        ) : this.state.modulo === "viewPendientes" ? (
          <ModuleViewPendientes />
        ) : this.state.modulo === "viewHome" ? (
          <ModuleViewHome />
        ) : this.state.modulo === "viewProveedores" ? (
          <ModuleViewProveedores />
        ) : this.state.modulo === "viewListDetails" ? (
          <ModuleViewDetails />
        ) : (
          <ModuleViewHome />
        )}
      </div>
    );
  }
}
export default Main;
