import React, { Component } from "react";
import {  Table } from "reactstrap";
import {} from "reactstrap";
import ModalProcessView from "./modals/ModalPartView";
import swal from "sweetalert";
import ModalGen from "./modals/modalAddData";

class ModulViewProcessInfoGen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      currendProcess: "",
      processLs: [],
      partsLs: [],
    };
  }
  toggle = () => {
    this.setState({ modal: !this.state.modal });
    if (this.state.modal === true) this.props.refreshReport();
  };
  viewModalInfo = (currendProcess) => {
    this.setState({ currendProcess });
    if (currendProcess.idProcess === null || currendProcess.idProcess < 1) {
      swal({
        icon: "warning",
        text: "No existe factura, se creara una nueva, ¿OK? ",
        buttons: {
          cancel: true,
          confirm: true,
        },
      }).then((isConfirm) => {
        if (isConfirm) {
          document.getElementById("modalAdjuntarProcess").click();
        } else {
          swal({
            title: "Cancelado",
            timer: 500,
            // text: 'Candidates are successfully shortlisted!',
            icon: "error",
            buttons: false,
          });
        }
      });
    } else {
      this.setDataParts(currendProcess.idProcess);
      this.toggle();
      this.setState({ modal: !this.state.modal });
    }
  };
  componentDidMount() {}
  setDataFetch = async (data) => {
    const response = await fetch(
      "https://diniz.com.mx/diniz/servicios/services/mpagos_service.php",
      {
        method: "POST",
        body: data,
        header: new Headers({
          "Content-Type": "application/json",
        }),
      }
    );
    return await response.json();
  };
  setDataParts = (process) => {
    const data = new FormData();
    data.append("optionService", "SelectParts");
    data.append("process", process);
    this.setDataFetch(data).then((response) => {
      if (response.OK) {
        this.setState({ partsLs: response.json });
      } else this.setState({ partsLs: [] });
      console.log(response);
    });
  };
  asignarFactura = () => {
    const data = new FormData();
    data.append("optionService", "AsignarFactura");
    data.append("document", document.getElementById("inputFactura").value);
    data.append("idrecord", document.getElementById("inputRecordid").value);
    data.append("monto", document.getElementById("inputMonto").value);
    data.append(
      "inputCategory",
      document.getElementById("inputCategory").value
    );
    data.append("type", document.getElementById("inputtype").value);
    data.append("prov", document.getElementById("inputprov").value);
    data.append("noempl", document.getElementById("inputnoempl").value);

    this.setDataFetch(data).then(() => {
      this.props.refreshReport();
    });
  };
  render() {
    return (
      <>
        <ModalGen
          provLsSelect={this.state.provLsSelect}
          catLsSelect={this.state.catLsSelect}
          btnOk={this.asignarFactura}
          modalTip={"AsignarProcess"}
          hiddenBtnModal={true}
          currendProcess={this.state.currendProcess}
          refreshReport={this.props.refreshReport}
        />
        <ModalProcessView
          toggle={this.toggle}
          modal={this.state.modal}
          refreshReport={this.props.refreshReport}
          process={this.state.currendProcess}
          partsLs={this.state.partsLs}
          getDataParts={this.setDataParts}
        ></ModalProcessView>
        <div
          className="d-flex justify-content-center "
          style={{ marginTop: 20 }}
        >
          <div className="" style={{ width: "100%" }}>
            <div
              style={{ maxHeight: 420, overflow: "auto" }}
              className="NotScroll"
            >
              <Table className="table table-hover " style={{}}>
                <thead>
                  <tr>
                    {/* <th></th> */}
                    <th>Servicio</th>
                    <th>Descripcion</th>
                    <th>Fecha Esperada</th>
                    <th>Proveedor</th>
                    <th>Monto Esperado</th>
                    <th>Factura</th>
                    <th>Monto Real</th>
                    <th>OC</th>
                    <th>FP</th>
                    <th>Confirmacion</th>
                  </tr>
                </thead>
                <tbody style={{}}>
                  {this.props.reportLs.length > 0
                    ? this.props.reportLs.map((process, i) => (
                        <>
                          <tr
                            onClick={this.viewModalInfo.bind(this, process)}
                            key={i}
                            style={{background:process.variable === "1" ? "#D4E6F1" : ""}}
                          >
                            {/* <td>{process.idrecord}</td> */}
                            <td>{process.servicio}</td>
                            <td>{process.descripcionServ}</td>
                            <td>{process.date}</td>
                            <td>{process.prov}</td>
                            <td style={{ textAlign: "right" }}>
                              $
                              {parseInt(process.montoEsperado).toLocaleString('en-US')}{process.usd === "1" ? " USD" : " MXN"}
                            </td>
                            {process.factura ? (
                              <>
                                <td>{process.factura}</td>
                                <td  style={{ textAlign: "right" }}>{parseInt(process.montoReal).toLocaleString('en-US')} {process.usd === "1" ? " USD" : " MXN"}</td>
                                {parseInt(process.cantidadoc) - 1 === 0 ? (
                                  <>
                                    <td>{process.oc ? <><b>{process.oc}</b> / {process.dateOc.substr(0,10) }</> : ""}</td>
                                    <td>{process.fp ? <><b>{process.fp}</b> / {process.dateFp.substr(0,10) }</> : ""}</td>
                                    <td>{process.datePay}</td>
                                  </>
                                ) : parseInt(process.cantidadoc) > 1 ? (
                                  <td colSpan={6} className={"text-center"}>
                                    La factura contiene mas de un OC
                                  </td>
                                ) : (
                                  <td colSpan={6} className={"text-center"}>
                                    {/* Sin OC Registrados */}
                                  </td>
                                )}
                              </>
                            ) : (
                              <td colSpan={8} className={"text-center"}>
                                {/* Sin registro de Factura */}
                              </td>
                            )}
                          </tr>
                        </>
                      ))
                    : ""}
                </tbody>
              </Table>
            </div>
          </div>
        </div>
      </>
    );
  }
}
export default ModulViewProcessInfoGen;
