
import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";

const LoadingTato = (props) => {
  // let ok = true;
  const [animate, getAnimete] = useState();
  const [initial, getInitial] = useState({});
  const [timerLaps, getTimer] = useState(props.timer ? props.timer : 1 );
  const [finish, getfinish] = useState(
    Math.floor(Math.random() * (89 - 84) + 84)
  );
  const [porcentaje, getporcentaje] = useState(
    props.finish === false ? 0 : finish + 1
  );
  // const [finishb, getfinishb] = useState(props.finish);

  useEffect(() => {
    const timer = setInterval(
      () => {
        getporcentaje((porcentaje) =>
          // porcentaje > finish ? 99 : porcentaje + 1
          porcentaje < finish && props.finish === false
            ? porcentaje + 1
            : porcentaje === finish && props.finish === false
            ? finish
            : props.finish === true && porcentaje < 100
            ? porcentaje + 1
            : 100
        );
      },
      porcentaje <= finish-80  && props.finish === false
        ? 30 * timerLaps :
        porcentaje <= finish-65  && props.finish === false
        ? 50 * timerLaps
        : porcentaje <= finish-45 && props.finish === false
        ? 75 * timerLaps
        : porcentaje <= finish-35 && props.finish === false
        ? 120 * timerLaps
        : porcentaje <= finish-20 && props.finish === false
        ? 400 * timerLaps
        : porcentaje <= finish-15 && props.finish === false
        ? 750 * timerLaps
        : porcentaje <= finish-5 && props.finish === false
        ? 1000 * timerLaps
        : (porcentaje <= finish-3 )&& props.finish === false
        ? 2000 * timerLaps
        : (porcentaje <= finish-1 || porcentaje <= finish)&& props.finish === false
        ? 4000 * timerLaps
        : .001 * timerLaps
    );

    return () => {
      clearInterval(timer);
    };
  });

  return (
    <div style={{ width: "100%" }} className={"d-flex justify-content-center"}>
      <div
        style={{
          width: props.width,
          height: props.width * 1.5,
          background: "",
          position: "relative",
        }}
      >
        <motion.div
          initial={{ scale: 1, opacity: 1 }}
          animate={{ scale: 1.05, opacity: 0.8 }}
          transition={{
            ease: "linear",
            duration: 0.8,
            repeat: Infinity,
            repeatType: "reverse",
          }}
        >
          <div>
            <img
              src="./tato.png"
              style={{
                width: props.width,
                height: props.width * 1.5,
                filter: " grayscale(1)",
              }}
            ></img>
          </div>
          <motion.div
            initial={{
              position: "absolute",
              bottom: 0,
              background: "",
              overflow: "hidden",
              width: props.width,
              height: props.width * 0.01,
            }}
            animate={{
              height: (props.width * porcentaje * 1.5) / 100,
            }}
            transition={{
              duration: porcentaje <  35 && props.finish === false ? 0.1 : porcentaje <  55 && props.finish === false ? 0.35 : 0.001,
            }}
          >
            <img
              src="./tato.png"
              style={{
                position: "absolute",
                bottom: 0,
                width: props.width,
                height: props.width * 1.5,
                filter: "",
              }}
            />
          </motion.div>
          <p
            style={{
              position: "absolute",
              background: "white",
              top: props.width / 1,
              left: props.width / 2.4,
              borderRadius: 6000,
              width: 50,
              height: 50,
              padding:
                porcentaje < 10
                  ? "12px 12px"
                  : porcentaje > 99
                  ? "12px 1px"
                  : "12px 5px",
            }}
          >
            {"  "}
            {/*props.finish === true ? 100 :*/ porcentaje}%
          </p>
        </motion.div>
        <motion.div drag="asdasdasdkjas;lkjdlaskjdlkj" />
      </div>
    </div>
  );
};
export default LoadingTato;
