import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { Button, Col, Row, Input } from "reactstrap";
import React from "react";
import "./regprocess.css";
import { Component } from "react";
class ModalRegProcess extends Component {
  constructor() {
    super();
    this.state = {
      modal: false,
      provLs: [],
      categoryLs: [],
    };
  }
  setData = () => {
    const data = new FormData();
    data.append("prov", document.getElementById("inputProv").value);
    data.append("document", document.getElementById("inputDocument").value);
    data.append("type", document.getElementById("inputType").value);
    data.append("monto", document.getElementById("montoProcess").value);
    data.append("optionService", "InsertProcess");
    data.append("noempl", window.noempl);
    data.append("inputCategory",  document.getElementById("inputCategory").value);

    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    }).then(() => {
      this.props.setDataProcess();
    });
  };
  selectProvs = () => {
    const data = new FormData();
    data.append("optionService", "SelectProv");
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ provLs: response.json });
      });
  };
  selectCategory = () => {
    const data = new FormData();
    data.append("optionService", "SelectCategory");
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ categoryLs: response.json });
      });
  };
  toggle = () => {
    this.setState({ modal: !this.state.modal });
  };
  componentDidMount = () => {
    this.selectProvs();
    this.selectCategory();
  };
  render() {
    return (
      <>
        <Button
          color="btn btn-outline-primary btn-sm "
          style={{ fontSize: 11 }}
          title="Agregar Proceso"
          onClick={this.toggle}
        >
          <span className="fas fa-plus" />
        </Button>
        <Modal
          id="ModalUser1"
          isOpen={this.state.modal}
          toggle={this.toggle}
          size="lg"
          className="small"
        >
          <ModalHeader className="bg-info text-white">
            Generar Nuevo Proceso
          </ModalHeader>
          <ModalBody>
          <Row>
              <Col sm={4}>
                <p>
                  Proveedor
                  <Button
                    color="btn btn-outline-primary btn-sm "
                    style={{ fontSize: 11, marginLeft: 25 }}
                    title="Agregar parte"
                    onClick={this.viewProvs}
                  >
                    <span className="fas fa-eye" />
                  </Button>
                </p>
              </Col>
              <Col sm={4}>
                <p>Numero de documento</p>
              </Col>
              <Col sm={3}>
                <p>Monto</p>
              </Col>
            </Row>
            <Row>
              <Col sm={4}>
                <Input type="select" id="inputProv">
                  {this.state.provLs
                    ? this.state.provLs.map((proveedor, index) => (
                        <>
                          <option value={proveedor.id} index= {index}> {proveedor.noProv}-{proveedor.prov}</option>
                        </>
                      ))
                    : "a"}
                </Input>
              </Col>
              <Col sm={4}>
                <Input type="text" id="inputDocument"></Input>
              </Col>
              <Col sm={4}>
               
              <Input type="number" id="montoProcess" />
              </Col>
            </Row>
            <Row>
              <Col sm={4}>
                <p>
                  Categoria
                </p>
              </Col>
              <Col sm={4}>
                <p>
                Tipo de Compra
                </p>
              </Col>
              {/* <Col sm={4}>
                <p>Numero de documento</p>
              </Col>
              <Col sm={3}>
                <p>Tipo de Compra</p>
              </Col> */}
            </Row>
            <Row>
              <Col sm={4}>
                <Input type="select" id="inputCategory">
                  {this.state.categoryLs
                    ? this.state.categoryLs.map((category, index) => (
                        <>
                          <option value={category.id} index= {index}> {category.description}</option>
                        </>
                      ))
                    : "a"}
                </Input>
              </Col>
              <Col sm={4}>
              <Input type="select" id="inputType">
                  <option value={1}> Pago de servicio</option>
                  <option value={2}> Compra interna</option>
                  <option value={1}> Compra a provedores directos</option>
                </Input>
              </Col>
              {/* <Col sm={3}>
                <Input type="text" id="inputDocument"></Input>
              </Col>
              <Col sm={4}>
                <Input type="select" id="inputType">
                  <option value={1}> Compra de servicio</option>
                  <option value={2}> Compra interna</option>
                  <option value={1}> Compra a provedores directos</option>
                </Input>
              </Col> */}
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" size="sm" outline onClick={this.toggle}>
              Cancelar
            </Button>
            <Button
              id="guardarPart"
              color="success"
              size="sm"
              outline
              onClickCapture={this.toggle}
              onClick={this.setData}
            >
              Guardar
            </Button>
          </ModalFooter>
        </Modal>
      </>
    );
  }
}
export default ModalRegProcess;
