import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { Button, Input, Table } from "reactstrap";
import React, {  } from "react";
import "./regprocess.css";
import { Component } from "react";
class ModalViewDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      detallesLs: [],
      list: false,
    };
  }
  setDateDetails = (e) => {
    const data = new FormData();
    data.append("optionService", "SelectDetailsList");
    data.append("conjunto", e.target.value);
    if (e.target.value !== "-") this.setState({ list: e.target.value });
    else this.setState({ list: false });
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ detallesLs: response.json });
      });
  };
  validarDato = (e) => {
    let id = e.target.id.split("-");
    let nota = document.getElementById("Nota-" + id[1]);
    let costo = document.getElementById("costo-" + id[1]);
    if (parseInt(costo.value) < parseInt(e.target.value)) nota.hidden = false;
    else nota.hidden = true;
  };
  componentWillUnmount= () => { 
    this.setState({list:false})
  }
  saveDetails = () => {
    // var campoVacio = false;
    var values = [];
    const data = new FormData();
    data.append("optionService", "InsertDetails");
    this.state.detallesLs.map((element) => {
      let montoReal = document.getElementById("MontoReal-" + element.id);
      let nota = document.getElementById("Nota-" + element.id);
      data.append("values[]", [
        element.id,
        this.props.partSelect,
        montoReal.value,
        nota.value,
      ]);
      // if (montoReal.value === "" || montoReal.value < 0.0001) campoVacio = true;
    });

    console.log(values);
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        // console.log(response);
        this.setState({ list: false });
        this.props.viewDetails(false, this.props.partSelect);
        // this.setState({ detallesLs: response.json });
      });

    // if(campoVacio) alert("existe un campo vacio")
  };
  RegistrarDetalles = () => {
    return (
      <>
        <div>
          <p>Selecciona la lista y llena los campos</p>
          <input
            type="text"
            hidden={true}
            defaultValue={this.props.partSelect}
          />
          <Input type="select" onChange={this.setDateDetails} id="inputList">
            <option value={"-"}></option>
            <option value={"COMUNICACIONES"}> COMUNICACIONES</option>
            <option value={"TELEFONIA"}> TELEFONIA</option>
          </Input>
        </div>
        <Table>
          {this.state.list === "COMUNICACIONES" ? (
            <>
              <thead>
                <tr>
                  <th>Empresa</th>
                  <th>Nunmero</th>
                  <th>Ubicacion</th>
                  <th>Tipo Paquete</th>
                  <th>Costo del plan</th>
                  <th>Costo real</th>
                </tr>
              </thead>
              <tbody>
                {this.state.detallesLs.length
                  ? this.state.detallesLs.map((element, index) => (
                      <tr key={index}>
                        <td>{element.subconunto}</td>
                        <td>{element.description}</td>
                        <td>{element.origin}</td>
                        <td>{element.description2}</td>
                        <td>${element.costo}</td>
                        <td>
                          <Input
                            type="number"
                            style={{ width: 100, height: 25, fontSize: 12 }}
                            onBlur={this.validarDato}
                            id={"MontoReal-" + element.id}
                          />
                          <input
                            defaultValue={element.costo}
                            id={"costo-" + element.id}
                            hidden={true}
                          />
                        </td>
                        <td style={{ width: 215, height: 50 }}>
                          <Input
                            type="textarea"
                            hidden={true}
                            style={{ fontSize: 11, height: 10, width: 200 }}
                            id={"Nota-" + element.id}
                          />
                        </td>
                      </tr>
                    ))
                  : "b"}
              </tbody>
            </>
          ) : this.state.list === "TELEFONIA" ? (
            <>
              <thead>
                <tr>
                  <th>Conjunto</th>
                  <th>Usuario</th>
                  <th>Puesto</th>
                  <th>Empresa</th>
                  <th>Ubicacion</th>
                  <th>Vencimiento</th>
                  <th>Numero</th>
                  <th>Costo</th>
                </tr>
              </thead>
              <tbody>
                {this.state.detallesLs.length
                  ? this.state.detallesLs.map((element, index) => (
                      <tr key={index}>
                        <td>{element.subconunto}</td>
                        <td>{element.user}</td>
                        <td>{element.puesto}</td>
                        <td>{element.empresa}</td>
                        <td>{element.origin}</td>
                        <td>{element.fechaVencimiento}</td>
                        <td>{element.description}</td>
                        <td>${element.costo}</td>
                        <td>
                          <Input
                            type="number"
                            style={{ width: 100, height: 25, fontSize: 12 }}
                            onBlur={this.validarDato}
                            id={"MontoReal-" + element.id}
                          />
                          <input
                            defaultValue={element.costo}
                            id={"costo-" + element.id}
                            hidden={true}
                          />
                        </td>
                        <td style={{ width: 215, height: 50 }}>
                          <Input
                            type="textarea"
                            hidden={true}
                            style={{ fontSize: 11, height: 10, width: 200 }}
                            id={"Nota-" + element.id}
                          />
                        </td>
                      </tr>
                    ))
                  : "b"}
              </tbody>
            </>
          ) : (
            ""
          )}
        </Table>
      </>
    );
  };
  render() {
    return (
      <>
        <Modal
          backdrop="static"
          id="ModalUser1"
          isOpen={this.props.modal}
          toggle={this.props.toggle}
          size="xl"
          className="small"
        >
          <ModalHeader className="bg-info text-white"></ModalHeader>
          <ModalBody>
            {this.props.detailsLs.length ? (
              <Table>
                <thead>
                  {this.props.detailsLs.length ? (
                    this.props.detailsLs[0].conjunto === "TELEFONIA" ? (
                      <tr>
                        <th>Conjunto</th>
                        <th>Usuario</th>
                        <th>Puesto</th>
                        <th>Empresa</th>
                        <th>Ubicacion</th>
                        <th>Vencimiento</th>
                        <th>Numero</th>
                        <th>Costo</th>
                        <th>Costo Real</th>
                        <th>Nota</th>
                      </tr>
                    ) :(
                      <tr>
                        <th>Conjunto</th>
                        <th>Empresa</th>
                        <th>Ubicacion</th>
                        <th>Vencimiento</th>
                        <th>Numero</th>
                        <th>Costo</th>
                        <th>Costo Real</th>
                        <th>Nota</th>
                      </tr>
                    )
                  ) : (
                    ""
                  )}
                </thead>
                <tbody>
                  {this.props.detailsLs.length
                    ? this.props.detailsLs.map((element) => (
                      this.props.detailsLs[0].conjunto === "TELEFONIA" ? (
                        <tr>
                        <td>{element.subconunto}</td>
                        <td>{element.user}</td>
                        <td>{element.puesto}</td>
                        <td>{element.empresa}</td>
                        <td>{element.origin}</td>
                        <td>{element.fechaVencimiento}</td>
                        <td>{element.description}</td>
                        <td>${element.costo}</td>
                        <td>${element.montoReal}</td>
                        <td>{element.nota}</td>
                      </tr>
                      ) :(
                        <tr>
                        <td>{element.subconunto}</td>
                        <td>{element.empresa}</td>
                        <td>{element.origin}</td>
                        <td>{element.fechaVencimiento}</td>
                        <td>{element.description}</td>
                        <td>${element.costo}</td>
                        <td>${element.montoReal}</td>
                        <td>{element.nota}</td>
                      </tr>
                      )
                    ) )
                    : "b"}
                </tbody>
              </Table>
            ) : (
              this.RegistrarDetalles()
            )}

            {/*  */}
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              size="sm"
              outline
              onClick={() => {
                this.setState({list: false});
                this.props.toggle()
              }}
            >
              Cancelar
            </Button>
            <Button
              id="guardarPart"
              color="success"
              size="sm"
              outline
              // onClickCapture={this.props.toggle}
              onClick={this.saveDetails}
            >
              Guardar
            </Button>
          </ModalFooter>
        </Modal>
      </>
    );
  }
}
export default ModalViewDetails;
