// import { Tab } from "bootstrap";
import React, { Component } from "react";
import { Input, Table } from "reactstrap";
import ModalGen from "./modals/modalAddData";
class ModuleViewDetails extends Component {
  constructor() {
    super();
    this.state = { detailsLs: false, detailSelect: false };
  }
  dataFetch = async (data) => {
    const response = await fetch(
      "https://diniz.com.mx/diniz/servicios/services/mpagos_service.php",
      {
        method: "POST",
        body: data,
        header: new Headers({
          "Content-Type": "application/json",
        }),
      }
    );
    return await response.json();
  };
  selectDetails = () => {
    let value = document.getElementById("listDetails").value;
    const data = new FormData();
    data.append("optionService", "SelectDetailsList");
    data.append("conjunto", value);
    this.dataFetch(data).then((response) => {
      console.log(response.json);
      this.setState({ detailsLs: response.json });
    });
    // alert(value)
  };
  openModalEdit = (detail) => {
    this.setState({ detailSelect: detail });
    document.getElementById("modalAdjuntarProcess").click();
  };
  componentDidMount = () => {
    this.selectDetails();
  };
  editDetail = () => { 
    const data = new FormData();
    data.append("optionService", "UpdateDetailForList");
    data.append("costo", document.getElementById('inputCosto').value);
    data.append("vencimiento", document.getElementById('inputVencimiento').value);
    data.append("puesto", document.getElementById('inputPuesto').value);
    data.append("user", document.getElementById('inputUser').value);
    data.append("origin", document.getElementById('inputOrigin').value);
    data.append("conjunto", document.getElementById('inputConjunto').value);
    data.append("empresa", document.getElementById('inputEmpresa').value);
    data.append("idDetail", document.getElementById('inputIdDetail').value);
    data.append("description", document.getElementById('inputDescription').value);
    data.append("detailList", document.getElementById('inputDetailList').value);
    this.dataFetch(data).then(()=>this.selectDetails())
    // console.log(data)
  }
  insertDetail = () => { 
    const data = new FormData();
    data.append("optionService", "InsertDetailForList");
    data.append("costo", document.getElementById('inputCosto').value);
    data.append("vencimiento", document.getElementById('inputVencimiento').value);
    data.append("puesto", document.getElementById('inputPuesto').value);
    data.append("user", document.getElementById('inputUser').value);
    data.append("origin", document.getElementById('inputOrigin').value);
    data.append("conjunto", document.getElementById('inputConjunto').value);
    data.append("empresa", document.getElementById('inputEmpresa').value);
    data.append("description", document.getElementById('inputDescription').value);
    data.append("detailList", document.getElementById('inputDetailList').value);
    this.dataFetch(data).then(()=>this.selectDetails())
    // console.log(data)
  }
  render() {
    return (
      <div>
        <ModalGen
          modalTip={"Details"}
          hiddenBtnModal={true}
          detailSelect={this.state.detailSelect}
          btnOk={this.editDetail}
        />
        <div>
          <Input type="select" id="listDetails" onChange={this.selectDetails}>
            <option value={"TELEFONIA MOVIL"}>TELEFONIA MOVIL</option>
            <option value={"COMUNICACIONES TELMEX"}>COMUNICACIONES TELMEX</option>
            <option value={"COMUNICACIONES TOTALPLAY"}>COMUNICACIONES TOTALPLAY</option>
            <option value={"COMUNICACIONES ALESTRA"}>COMUNICACIONES ALESTRA</option>
          </Input>
        </div>
        <div>
          <Table>
            <thead>
              <tr>
                <th>
                  <ModalGen
                    modalTip={"Details"}
                    hiddenBtnModal={false}
                    detailSelect={false}
                    btnOk={this.insertDetail}
                  />
                </th>
                <th>Empresa</th>
                <th>Conjunto</th>
                <th>Origen</th>
                <th>Usuario</th>
                <th>Puesto</th>
                <th>Descripcion</th>
                <th>Vencimiento</th>
                <th>Costo</th>
              </tr>
            </thead>
            <tbody>
              {this.state.detailsLs
                ? this.state.detailsLs.map((element) => (
                    <tr onClick={this.openModalEdit.bind(this, element)}>
                      <td></td>
                      <td>{element.empresa}</td>
                      <td>{element.subconunto}</td>
                      <td>{element.origin}</td>
                      <td>{element.user}</td>
                      <td>{element.puesto}</td>
                      <td>{element.description}</td>
                      <td>{element.fechaVencimiento}</td>
                      <td>${element.costo}</td>
                    </tr>
                  ))
                : ""}
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}
export default ModuleViewDetails;
