import React, { Component } from "react";
import { Table } from "reactstrap";
import {} from "reactstrap";

import ModalGen from "./modals/modalAddData";

class ModuleViewPendientes extends Component {
  constructor() {
    super();
    this.state = {
      provLsSelect: [],
      catLsSelect: [],
      recordLs: [],
    };
  }
  getProvData = () => {
    const data = new FormData();
    data.append("optionService", "SelectProv");
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        // this.setState({ proveedoresLs: response.json });
        let lsSelect = response.json.map((element, index) => {
          return { value: element.id, text: element.prov };
        });
        this.setState({ provLsSelect: lsSelect });
      });
  };
  getCatData = () => {
    const data = new FormData();
    data.append("optionService", "SelectCategory");
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        // this.setState({ proveedoresLs: response.json });
        let lsSelect = response.json.map((element, index) => {
          return { value: element.id, text: element.description };
        });
        this.setState({ catLsSelect: lsSelect });
      });
  };
  componentDidMount = () => {
    this.getProvData();
    this.getCatData();
    this.getRecordData();
  };
  addRep = () => {
    const data = new FormData();
    data.append("optionService", "InsertRepetido");
    data.append("day", document.getElementById("inputDay").value);
    data.append("type", document.getElementById("inputType").value);
    data.append("soli", document.getElementById("inputSolicitud").value);
    data.append("prov", document.getElementById("inputProv").value);
    data.append("category", document.getElementById("inputCategoria").value);
    data.append("description", document.getElementById("inputDescription").value);
    data.append("monto", document.getElementById("inputMonto").value);
    data.append("conjunto", document.getElementById("inputConjunto").value);
    data.append("mesPago", document.getElementById("inputMesPago").value);
    data.append("moneda", document.getElementById("inputMoneda").checked);
    data.append("variable", document.getElementById("inputVariable").checked);
    data.append("noempl", window.noempl);
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    }).then(() => {
      this.getRecordData();
    });
  };
  getRecordData = () => {
    const data = new FormData();
    data.append("optionService", "SelectRecord");
    data.append("noempl", window.noempl);
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ recordLs: response.json });
        // console.log(response.json.length);
        setTimeout(() => {
          // console.log(this.state.recordLs);
        }, 1000);
      });
  };
  render() {
    return (
      <>
        <div
          className="d-flex  align-items-center flex-column"
          style={{ marginTop: 20 }}
        >
          <div>
            <p className="text-center">Pagos programados</p>
            <Table>
              <thead>
                <tr>
                  <th>
                    <ModalGen
                      provLsSelect={this.state.provLsSelect}
                      catLsSelect={this.state.catLsSelect}
                      addRep={this.addRep}
                      modalTip={"NewProcessProgramado"}
                    />
                  </th>
                  <th>Proveedor</th>
                  <th>Descripcion</th>
                  <th>Dia</th>
                  <th>Solicitar Fac.</th>
                  <th>Categoria</th>
                </tr>
              </thead>
              <tbody>
                {
                  this.state.recordLs.map((element, index) => (
                    <tr>
                      <td>{/*element.id*/}</td>
                      <td>{element.prov}</td>
                      <td>{element.description}</td>
                      <td>{element.day}</td>
                      <td>{element.solicitud}</td>
                      <td>{element.servicio}</td>
                     
                    </tr>
                  ))
                }
              </tbody>
            </Table>
          </div>
        </div>
      </>
    );
  }
}
export default ModuleViewPendientes;
