import React, { Component } from "react";
import ModulViewProcessInfoGen from "./ModuleViewProcessInfoGeneral";
import Select from "react-select";
import LoadingTato from "./loadingTato";

class ModuleViewPendientes extends Component {
  mounthsOptions = [
    { value: "0", label: "Todos" },
    { value: "1", label: "Enero" },
    { value: "2", label: "Febrero" },
    { value: "3", label: "Marzo" },
    { value: "4", label: "Abril" },
    { value: "5", label: "Mayo" },
    { value: "6", label: "Junio" },
    { value: "7", label: "Julio" },
    { value: "8", label: "Agosto" },
    { value: "9", label: "Septiembre" },
    { value: "10", label: "Octubre" },
    { value: "11", label: "Noviembre" },
    { value: "12", label: "Diciembre" },
  ];
  constructor() {
    super();
    this.state = {
      categoryLs: [],
      provLs: [],
      factLs: [],
      reportLs: [],
      mounths: false,
      mountSelect: "",
      provSelect: "",
      catSelect: "",
      factSelect: "",
      value: "",
      anoLs: "",
      finish: false,
      timer: 0.5,
      consult: false,
      loading: false,
    };
  }
  colourOptions = [{ value: "chocolate", label: "Chocolate" }];

  getDataReport = async (type, search, extraInfo = "") => {
    const data = new FormData();
    data.append("optionService", "SelectOptionsReport");
    data.append("type", type);
    data.append("search", search);
    data.append("extraInfo", extraInfo);
    const response = await fetch(
      "https://diniz.com.mx/diniz/servicios/services/mpagos_service.php",
      {
        method: "POST",
        body: data,
        header: new Headers({
          "Content-Type": "application/json",
        }),
      }
    );
    return await response.json();
  };

  componentDidMount = () => {};
  onInputChange = (e, text) => {
    //console.log(e);
    //console.log(text.name);
  };
  refreshReport = () => {
    this.setState({ loading: true, consult: false });
    let extraInfo =
      this.state.provSelect +
      "-" +
      this.state.catSelect +
      "-" +
      this.state.anoLs;
    this.getDataReport("mes", this.state.mountSelect, extraInfo).then(
      (response) => {
        this.setState({
          reportLs: response.json,
          loading: true,
          consult: false,
        });
      }
    );
  };
  selectOptions = (e, is, b) => {
    var optionsString = "";
    let extraInfo = "";
    this.setState({ consult: false });
    e.map((element) => {
      optionsString += "'" + element.value + "',";
    });

    optionsString = optionsString.substr(0, optionsString.length - 1);
    if (is.name === "ano") {
      e.map((element) => {
        optionsString += "'" + element.value + "',";
      });

      optionsString = optionsString.substr(0, optionsString.length - 1);
      this.setState({
        mounths: false,
        provLs: [],
        factLs: [],
        reportLs: [],
        categoryLs: [],
      });

      this.getDataReport("inicial", optionsString).then((response) =>
        this.setState({ categoryLs: response.json, anoLs: optionsString })
      );
      return;
    } else if (is.name === "category") {
      this.setState({
        mounths: false,
        provLs: [],
        factLs: [],
        reportLs: [],
      });
      extraInfo = this.state.anoLs;
    } else if (is.name === "prov")
      this.setState({
        mounths: false,
        reportLs: [],
      });
    else if (is.name === "mes") {
      this.setState({ loading: true, consult: false });
      //console.log()
      e.map((mes, key) => {
        if (mes.label === "Todos") {
          e.map((el, key) => delete e[key]);
          this.mounthsOptions.map((element) => {
            if (element.value !== "0") e.push(element);
          });
          //console.log(e);
        }
      });
      e.map((element) => {
        optionsString += "'" + element.value + "',";
      });

      optionsString = optionsString.substr(0, optionsString.length - 1);
      extraInfo =
        this.state.provSelect +
        "-" +
        this.state.catSelect +
        "-" +
        this.state.anoLs;
    }
    this.getDataReport(is.name, optionsString, extraInfo).then((response) => {
      ////console.log(response);
      if (is.name === "mes"){
        this.setState({
          mountSelect: optionsString,
          reportLs: response.json,
          finish: true,
        });
      
        setTimeout(() => {
          this.setState({
            loading: false,
            consult: true,
            finish: false,
          });
        }, 500);}
      if (is.name === "category")
        this.setState({
          catSelect: optionsString,
          provLs: response.json,
        });
      if (is.name === "prov")
        this.setState({
          mounths: true,
          provSelect: optionsString,
        });
    });
  };
  render() {
    return (
      <div>
        <div className="d-flex justify-content-center">
          <div
            style={{ width: 300, maxWidth: 300, margin: "0px 10px 0px 20px" }}
          >
            <p className="text-center">Año</p>
            <Select
              placeholder={<div className="Select-placeholder">Servicio</div>}
              id="SSS"
              ref={this.state.value}
              closeMenuOnSelect={false}
              isMulti
              isClearable
              isSearchable
              name="ano"
              onChange={this.selectOptions}
              options={[
                { value: "2022", label: "2022" },
                { value: "2023", label: "2023" },
              ]}
              // menuIsOpen={true}
            />
          </div>
          <div
            style={{ width: 300, maxWidth: 300, margin: "0px 10px 0px 20px" }}
          >
            <p className="text-center">Servicio</p>
            {this.state.categoryLs.length > 0 ? (
              <Select
                placeholder={<div className="Select-placeholder">Servicio</div>}
                id="SSS"
                ref={this.state.value}
                closeMenuOnSelect={false}
                isMulti
                isClearable
                isSearchable
                name="category"
                onChange={this.selectOptions}
                options={this.state.categoryLs}
                // menuIsOpen={true}
              />
            ) : (
              <>
                <Select />
              </>
            )}
          </div>
          <div style={{ width: 600, margin: "0px 10px" }}>
            <p className="text-center">Proveedor</p>
            {this.state.provLs.length > 0 &&
            this.state.categoryLs.length > 0 ? (
              <Select
                closeMenuOnSelect={false}
                isMulti
                isSearchable
                name="prov"
                onChange={this.selectOptions}
                options={this.state.provLs}
                // menuIsOpen={menuIsOpen}
              />
            ) : (
              <>
                <Select />
              </>
            )}
          </div>
          <div style={{ width: 800, maxWidth: 800, margin: "0px 20px 0 10px" }}>
            <p className="text-center">Mes</p>

            {this.state.mounths ? (
              <Select
                closeMenuOnSelect={false}
                isMulti
                // defaultValue={this.colourOptions[0]}
                isSearchable
                name="mes"
                onChange={this.selectOptions}
                options={this.mounthsOptions}
                // menuIsOpen={menuIsOpen}
              />
            ) : (
              <>
                <Select />
              </>
            )}
          </div>
        </div>
        <div className="d-flex bg-" style={{ maxHeight: "70vh" }}>
          <div
            className="NotScroll"
            style={{
              width: "100vw",
              padding: "0 10px",
              overflowY: "auto",
            }}
          >
            {this.state.consult ? (
              <ModulViewProcessInfoGen
                reportLs={this.state.reportLs}
                refreshReport={this.refreshReport}
              />
            ) : this.state.loading ? (
              <LoadingTato width={300} finish={this.state.finish} />
            ) : (
              ""
            )}
          </div>
        </div>
      </div>
    );
  }
}
export default ModuleViewPendientes;
