import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  FormGroup,
} from "reactstrap";
import { Button, Label, Input, Table } from "reactstrap";
import React from "react";
import "./regprocess.css";
import { Component } from "react";
import swal from "sweetalert";
import ModalViewDetails from "./ModalViewDetails";
class ModalProcessView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalLog: false,
      partsLs: [],
      detailsLs: [],
    };
  }
  AddPart = () => {
    let idDocRef = document.getElementById("idDocRef").value;
    let oc = document.getElementById("oc").value;
    let mProcess = parseFloat(document.getElementById("montoProcess").value);
    let mOc = parseFloat(document.getElementById("montoOc").value);
    let cuentaOc = document.getElementById("cuentaOc").value;
    let partidaOc = document.getElementById("partidaOc").value;
    let empresaOc = document.getElementById("empresaOc").value;
    if (oc !== "" && mOc !== "" && oc > 0 && mOc > 0 && mOc <= mProcess) {
      //console.log("aaa");
      const data = new FormData();
      data.append("optionService", "InsertPart");
      data.append("oc", oc);
      data.append("type", document.getElementById("typeProcess").value);
      data.append("process", idDocRef);
      data.append("cuentaOc", cuentaOc);
      data.append("partidaOc", partidaOc);
      data.append("empresaOc", empresaOc);
      oc = "";
      fetch(
        "https://diniz.com.mx/diniz/servicios/services/mpagos_service.php",
        {
          method: "POST",
          body: data,
          header: new Headers({
            "Content-Type": "application/json",
          }),
        }
      ).then(() => {
        this.props.getDataParts(document.getElementById("idDocRef").value);
      });
    } else if (mOc > mProcess) {
      alert("Monto de oc mayor al total de la factura");
    }
  };
  nextState = (part) => {
    swal({
      icon: "warning",
      text: "¿Quieres pasar al siguiente estado?",
      buttons: {
        cancel: true,
        confirm: true,
      },
    }).then((isConfirm) => {
      if (isConfirm) {
        const data = new FormData();
        data.append("optionService", "UpdatePartState");
        data.append("id", part.id);
        data.append("state", part.state);
        data.append("nextState", part.nextState);
        data.append("nextId", part.idNext);
        data.append("type", document.getElementById("typeProcess").value);

        fetch(
          "https://diniz.com.mx/diniz/servicios/services/mpagos_service.php",
          {
            method: "POST",
            body: data,
            header: new Headers({
              "Content-Type": "application/json",
            }),
          }
        ).then(() => {
          this.props.getDataParts(document.getElementById("idDocRef").value);
          //this.props.setDataProcess();
        });

        swal({
          title: "Estado Actualizado",
          timer: 1000,
          // text: 'Candidates are successfully shortlisted!',
          icon: "success",
          buttons: false,
        }).then(function () {
          // form.submit(); // <--- submit form programmatically
        });
      } else {
        swal({
          title: "Cancelado",
          timer: 500,
          // text: 'Candidates are successfully shortlisted!',
          icon: "error",
          buttons: false,
        });
      }
    });
  };
  viewExtraPart = (partNow) => {
    const data = new FormData();
    data.append("optionService", "SelectLogPart");
    data.append("id", partNow.id);
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ logLs: response.json });
        //console.log(response);
      });
    this.toggle();
  };
  updatePart = (e) => {
    // alert();
    let info = e.target.id.split("-");
    let value = "";
    if (info[1] === "fp" || info[1] === "ov") value = e.target.value;

    if (value !== "" || info[1] === "payment" || info[1] === "oCContadores") {
      //console.log(value);
      const data = new FormData();
      data.append("optionService", "UpdatePart");
      data.append("id", info[0]);
      data.append("value", value);
      data.append("type", info[1]);

      fetch(
        "https://diniz.com.mx/diniz/servicios/services/mpagos_service.php",
        {
          method: "POST",
          body: data,
          header: new Headers({
            "Content-Type": "application/json",
          }),
        }
      ).then(() => {
        this.props.getDataParts(document.getElementById("idDocRef").value);
      });
    }
  };
  toggle = () => {
    this.setState({ modalLog: !this.state.modalLog });
  };
  viewDetails = (e, info = false) => {
    // parentNode
    let id;
    if (info === false && e !== false) {
      id = e.target.id;

      if (e.target.localName !== "button" && !info) {
        // console.log(e.)
      }
    } else {
      id = info;
    }
    const data = new FormData();
    data.append("optionService", "SelectDetails");
    data.append("id", id);
    this.setState({ partSelect: id });
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ detailsLs: response.json });
      });
    if (e !== false) this.toggle();
  };
  componentDidUpdate = () => {
    // this.getData();
  };
  render() {
    return (
      <>
        <ModalViewDetails
          modal={this.state.modalLog}
          toggle={this.toggle}
          detailsLs={this.state.detailsLs}
          viewDetails={this.viewDetails}
          partSelect={this.state.partSelect}
        />
        <Modal
          backdrop="static"
          id="ModalUser1"
          isOpen={this.props.modal}
          toggle={this.props.toggle}
          size="xl"
          className="small"
        >
          <ModalHeader className="bg-info text-white">
            Factura: {this.props.process.factura}
          </ModalHeader>
          <ModalBody>
            <div className="d-flex align-items-center" style={{}}>
              <FormGroup floating>
                <Input
                  type="number"
                  id="oc"
                  placeholder="oc"
                  style={{ height: 40, }}
                />
                <Label for="oc" style={{ paddingTop: 7.5 }}>
                  OC
                </Label>
              </FormGroup>
              <FormGroup floating>
                <Input
                  type="number"
                  bsSize="md"
                  id="montoOc"
                  placeholder="Monto"
                  style={{ height: 40 }}
                />
                <Label for="oc" style={{ paddingTop: 7.5 }}>
                  Monto
                </Label>
              </FormGroup>
              <FormGroup floating>
                <Input
                  type="number"
                  bsSize="md"
                  id="cuentaOc"
                  placeholder="Cuenta"
                  style={{ height: 40 }}
                />
                <Label for="oc" style={{ paddingTop: 7.5 }}>
                  Cuenta
                </Label>
              </FormGroup>
              <FormGroup floating>
                <Input
                  type="number"
                  bsSize="md"
                  id="partidaOc"
                  placeholder="Partida"
                  style={{ height: 40 }}
                />
                <Label for="oc" style={{ paddingTop: 7.5 }}>
                  Partida
                </Label>
              </FormGroup>
              <FormGroup floating>
                <Input
                  type="select"
                  bsSize="md"
                  id="empresaOc"
                  placeholder="Empresa"
                  style={{ height: 40, padding: "15px 30px 0 10px " }}
                >
                  <option value={"RECREFAM"}> RECREFAM</option>
                  <option value={"OPARDIV"}>OPARDIV</option>
                  <option value={"FRANQUICIAS"}>FRANQUICIAS</option>
                  <option value={"SERVICIOS"}>SERVICIOS</option>
                </Input>
                <Label for="empresaOc" style={{ paddingTop: 7.5 }}>
                  Empresa
                </Label>
              </FormGroup>

              <Input
                defaultValue={this.props.process.idProcess}
                id="idDocRef"
                hidden={true}
              />
              <Input
                defaultValue={this.props.process.idCategory}
                id="typeProcess"
                hidden={true}
              />
              <Input
                defaultValue={this.props.process.montoReal}
                id="montoProcess"
                hidden={true}
              />
              <Button
                color="btn btn-outline-primary btn-sm "
                style={{ fontSize: 20, marginLeft: 5 }}
                title="Agregar parte"
                onClick={this.AddPart}
              >
                <span className="fas fa-plus" />
              </Button>
            </div>
            <Table className="table table-hover" style={{}}>
              <thead>
                <tr>
                  {/* <th></th> */}
                  <th>id</th>
                  <th>OC</th>
                  <th>Registro de OC</th>
                  <th>Envio OC</th>

                  {this.props.process.type === "2" ? (
                    <>
                      <th>OV</th>
                      <th>Fecha de OV</th>
                    </>
                  ) : (
                    ""
                  )}
                  <th>FP</th>
                  <th>Fecha de FP</th>
                  <th>Confirmacion en flujo</th>
                  <th>Detalle</th>
                </tr>
              </thead>
              <tbody>
                {this.props.partsLs.length > 0
                  ? this.props.partsLs.map((element) => (
                      <tr>
                        {/* <td>
                          <Button
                            color="btn btn-outline-primary btn-sm "
                            style={{ fontSize: 11 }}
                            title="Informacion Extra"
                            onClick={this.viewExtraPart.bind(this, element)}
                          >
                            <span className="fas fa-eye" />
                          </Button>
                        </td> */}
                        <td>{element.id}</td>
                        <td>{element.oc}</td>
                        <td style={{ color: "#708090" }}>
                          {element.dateOc}
                        </td>{" "}
                        <td style={{ color: "#708090" }}>
                          {element.dateOcContadores === null ? (
                            <Button
                              color="btn btn-outline-primary btn-sm "
                              style={{ fontSize: 11, marginLeft: 50 }}
                              title="Confirmacion de flujo"
                              onClick={this.updatePart}
                              id={element.id + "-oCContadores"}
                            >
                              <span
                                className="fas fa-check"
                                id={element.id + "-oCContadores"}
                              />
                            </Button>
                          ) : (
                            element.dateOcContadores
                          )}
                        </td>
                        {this.props.process.type === "2" ? (
                          <>
                            {" "}
                            <td>
                              {element.ov === null ? (
                                <Input
                                  id={element.id + "-ov"}
                                  onBlur={this.updatePart}
                                  style={{
                                    width: 100,
                                    height: 29,
                                    fontSize: 11,
                                  }}
                                ></Input>
                              ) : (
                                element.ov
                              )}
                            </td>{" "}
                            <td style={{ color: "#708090" }}>
                              {" "}
                              {element.dateOv}
                            </td>
                          </>
                        ) : (
                          ""
                        )}
                        <td>
                          {element.fp === null ? (
                            <Input
                              id={element.id + "-fp"}
                              onBlur={this.updatePart}
                              style={{ width: 100, height: 29, fontSize: 11 }}
                            ></Input>
                          ) : (
                            element.fp
                          )}
                        </td>
                        <td style={{ color: "#708090" }}>{element.dateFp}</td>
                        <td style={{ color: "#708090" }}>
                          {element.payment === null ? (
                            <Button
                              color="btn btn-outline-primary btn-sm "
                              style={{ fontSize: 11, marginLeft: 50 }}
                              title="Confirmacion de flujo"
                              onClick={this.updatePart}
                              id={element.id + "-payment"}
                            >
                              <span
                                className="fas fa-check"
                                id={element.id + "-payment"}
                              />
                            </Button>
                          ) : (
                            element.datePay
                          )}
                        </td>
                        <td>
                          <Button
                            color="btn btn-outline-primary btn-sm "
                            style={{ fontSize: 11, marginLeft: 10 }}
                            title="Ver detalles"
                            onClick={this.viewDetails}
                            id={element.id}
                          >
                            <span className={"fas fa-eye"} id={element.id} />
                          </Button>
                        </td>
                      </tr>
                    ))
                  : "Sin registros..."}
              </tbody>
            </Table>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              size="sm"
              outline
              onClick={this.props.toggle}
            >
              Cancelar
            </Button>
            {/* <Button
              id="guardarPart"
              color="success"
              size="sm"
              outline
              onClickCapture={this.props.toggle}
              onClick={this.setData}
            >
              Guardar
            </Button> */}
          </ModalFooter>
        </Modal>
      </>
    );
  }
}
export default ModalProcessView;
