import React, { Component } from "react";
import { Table } from "reactstrap";
import {} from "reactstrap";
import ModalGen from "./modals/modalAddData";

class ModuleViewProveedores extends Component {
  constructor() {
    super();
    this.state = {
      proveedoresLs: [],
    };
  }
  addProv = () => {
    const data = new FormData();
    data.append("optionService", "InsertProv");
    data.append("noempl", window.noempl);
    data.append("noProv", document.getElementById("inputNoProv").value);
    data.append("nameProv", document.getElementById("inputNameProv").value);
    data.append("des", document.getElementById("inputDescripcionProv").value);
    data.append("name", document.getElementById("inputNameConProv").value);
    data.append("numProv", document.getElementById("inputNumbProv").value);
    data.append("mailProv", document.getElementById("inputMailProv").value);
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    }).then(() => {
      this.setProv();
    });
  };
  setProv = () => {
    const data = new FormData();
    data.append("optionService", "SelectProv");
    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ proveedoresLs: response.json });
      });
  };
  componentDidMount = () => {
    this.setProv();
  };
  render() {
    return (
      <>
        <div
          className="d-flex justify-content-center"
          style={{ marginTop: 20 }}
        >
          <div>
            <Table>
              <thead>
                <tr>
                  <th>
                    <ModalGen modalTip="NewProveedor" btnOk={this.addProv} />
                  </th>
                  <th>No Proveedor</th>
                  <th>Proveedor</th>
                  <th>Descripcion</th>
                  <th>Contacto</th>
                  <th>Telefono</th>
                  <th>Correo</th>
                </tr>
              </thead>
              <tbody>
                {this.state.proveedoresLs.map((element, index) => (
                  <tr>
                    <td>{/*element.id*/}</td>
                    <td>{element.noProv}</td>
                    <td>{element.prov}</td>
                    <td>{element.description}</td>
                    <td>{element.name}</td>
                    <td>{element.phone}</td>
                    <td>{element.mail}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        </div>
      </>
    );
  }
}
export default ModuleViewProveedores;
