import React, { Component } from "react";
import { Input, Table } from "reactstrap";
import {
} from "reactstrap";

import ModalRegProcess from "./modals/ModalRegProcess";
import ModalProcessView from "./modals/ModalPartView";

class ModulViewProcess extends Component {
  constructor() {
    super();
    this.state = {
      modal: false,
      currendProcess: "",
      processLs: [],
      partsLs: [],
    };
  }
  toggle = () => {
    this.setState({ modal: !this.state.modal });
  };
  viewModalInfo = (currendProcess) => {
    this.setDataParts(currendProcess.id);
    this.toggle();
    this.setState({ currendProcess, modal: !this.state.modal });
  };
  setData = () => {
    const data = new FormData();
    data.append("optionService", "SelectProcess");
    data.append("noempl", window.noempl);

    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        if (response.OK) {
          this.setState({ processLs: response.json });
        } else this.setState({ processLs: [] });
        console.log(response);
      });
  };
  componentDidMount() {
    this.setData();
  }
  setDataParts = (process) => {
    const data = new FormData();
    data.append("optionService", "SelectParts");
    data.append("process", process);

    fetch("https://diniz.com.mx/diniz/servicios/services/mpagos_service.php", {
      method: "POST",
      body: data,
      header: new Headers({
        "Content-Type": "application/json",
      }),
    })
      .then((response) => response.json())
      .then((response) => {
        if (response.OK) {
          this.setState({ partsLs: response.json });
        } else this.setState({ partsLs: [] });
        console.log(response);
      });
  };
  render() {
    return (
      <>
        <ModalProcessView
          toggle={this.toggle}
          modal={this.state.modal}
          process={this.state.currendProcess}
          partsLs={this.state.partsLs}
          getDataParts={this.setDataParts}
        ></ModalProcessView>
        <div
          className="d-flex justify-content-center "
          style={{ marginTop: 20 }}
        >
          <div className="" style={{}}>
            <Input type="select">
              <option value={1}>Abiertos</option>
              <option value={2}>Cerrados</option>
              <option value={3}>All</option>
            </Input>
            <div style={{ maxHeight: 420, overflow: "auto" }}>
              <Table className="table table-hover " style={{ maxWidth: 800 }}>
                <thead>
                  <tr>
                    <th>
                      <ModalRegProcess
                        setDataProcess={this.setData}
                      ></ModalRegProcess>{" "}
                      
                    </th>
                    <th>Documento</th>
                    <th>Monto</th>
                    <th>Estado</th>
                    <th>Tipo</th>
                  </tr>
                </thead>
                <tbody style={{}}>
                  {" "}
                  {this.state.processLs.length > 0
                    ? this.state.processLs.map((process, i) => (
                        <tr
                          onClick={this.viewModalInfo.bind(this, process)}
                          key={i}
                        >
                          <td>{process.id}</td>
                          <td>{process.document}</td>
                          <td>${process.monto}</td>
                          <td>{process.open === "1" ? "Abierto" : "Terminado"}</td>
                          <td>
                            {process.type === "1"
                              ? "Pago de Servicio"
                              : process.type === "2"
                              ? "Compra Interna"
                              : "Pago Proveedores Directo"}
                          </td>
                        </tr>
                      ))
                    : "a"}
                </tbody>
              </Table>
            </div>
          </div>
        </div>
      </>
    );
  }
}
export default ModulViewProcess;
