import React, { useState } from 'react';
import {
  Nav,
  Dropdown,
  DropdownItem,
  DropdownToggle,
  DropdownMenu,
  NavItem,
  NavLink,
} from 'reactstrap';

function NavBar(props) {
  // const [dropdownOpen, setDropdownOpen] = useState(false);
  const [dropdownOpen2, setDropdownOpen2] = useState(false);

  // const toggle = () => setDropdownOpen(!dropdownOpen);
  const toggle2 = () => setDropdownOpen2(!dropdownOpen2);
  return (
    <div className="d-flex justify-content-center" style={{ marginTop: 25 }}>
      <Nav pills style={{ maxWidth: 1000 }}>
        <NavItem onClick={props.selectModule.bind(this, "viewHome")}>
          <NavLink href="#" style={{ marginRight: 20 }}>
            Informe
          </NavLink>
        </NavItem>
        
        <Dropdown
          nav
          isOpen={dropdownOpen2}
          toggle={toggle2}
          style={{ marginRight: 20 }}
        >
          <DropdownToggle nav caret>
            Consultas
          </DropdownToggle>

          <DropdownMenu>
            {/* <DropdownItem header>Detalles</DropdownItem> */}
            {/* <DropdownItem disabled> Por Factura</DropdownItem> */}
            {/* <DropdownItem onClick={props.selectModule.bind(this,"viewProcess")}> Documentos</DropdownItem> */}
            <DropdownItem
              onClick={props.selectModule.bind(this, "viewPendientes")}
            >
              {" "}
              Pagos Programados
            </DropdownItem>
            <DropdownItem
              onClick={props.selectModule.bind(this, "viewProveedores")}
            >
              {" "}
              Proveedores
            </DropdownItem>
            <DropdownItem
              onClick={props.selectModule.bind(this, "viewListDetails")}
            >
              Lista de detalles
            </DropdownItem>
            {/* <DropdownItem divider />
          <DropdownItem>Another Action</DropdownItem> */}
          </DropdownMenu>
        </Dropdown>
        {/* <Dropdown nav isOpen={dropdownOpen} toggle={toggle} style={{marginRight: 20 }}>
        <DropdownToggle nav caret>
          Informes
        </DropdownToggle>
        <DropdownMenu>
          <DropdownItem header>Detalles</DropdownItem> 
          <DropdownItem disabled> Por Factura</DropdownItem>
          <DropdownItem> Por Factura</DropdownItem>
          <DropdownItem>Por servicio</DropdownItem> 
          <DropdownItem divider />
          <DropdownItem>Another Action</DropdownItem>
        </DropdownMenu>
      </Dropdown> */}
        {/* <NavItem>
        <NavLink href="#">Link</NavLink>
      </NavItem>
      <NavItem>
        <NavLink href="#">Another Link</NavLink>
      </NavItem>
      <NavItem>
        <NavLink disabled href="#">
          Disabled Link
        </NavLink>
      </NavItem> */}
      </Nav>
    </div>
  );
}

export default NavBar;

